.location {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    width: 100%;

    margin-bottom: 120px;
}

.location span {
    text-align: center;
    width: 28%;
}

.location .map {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50%;
    height: 450px;

    margin-top: 60px;

    background-color: #aad3df;
    color: whitesmoke;

    border-radius: 20px;

    overflow: hidden;
    box-sizing: border-box;
    box-shadow: 0px 4px 12px 3px rgba(0, 0, 0, 0.25);
}

@media (max-width: 600px) {
    .location span{
        width: 80%;
    }
    .location .map{
        width: 85%;
        height: 250px;
    }
}